.form--lk {
	padding: 0 30%;
	color: #999999;
	@include tablets {
		padding: 0 20%;
	}
	@include phones {
		padding: 0;
	}
}

.profile__btns {
	display: flex;
	justify-content: space-between;
	flex-wrap: no-wrap;
	padding: 0 30%;
	@include tablets {
		padding: 0 20%;
	}
	@include phones {
		padding: 0;
	}
}

.form__group {
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 30px 20px;
	margin-bottom: 20px;
}

.form__title {
	font-size: 14px;
	text-align: center;
	margin-bottom: 20px;
}