.press {
	background: url('../img/dots-left-bg.png') 0% 50% no-repeat, url('../img/dots-right-bg.png') no-repeat 100% 50%, url('../img/eco-bg.jpg') no-repeat 50% 50% /cover;
	min-height: 700px;
	@include tablets {
		background: url('../img/eco-bg.jpg') no-repeat 50% 50% /cover;
	}
}

.press__title {
	color: #fff;
	text-align: center;
	font-size: 36px;
	font-weight: 600;
	@include phones {
		font-size: 32px;
	}
}


.press__content {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	padding: 50px 0;
}


.press__item {
	flex: 0 0 19%;
	min-height: 100%;
	background-color: #fff;
	opacity: .5;
	margin: 5px;
	transition: opacity .3s ease;
	height: 120px;
	&:hover {
		opacity: 1;
	}
	@include middle {
		flex: 0 0 49%;
	}
		@include phones {
		flex: 0 0 100%;
	}
}	

.press__link {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
}

.press__logo {
	max-width: 80%;
	height: auto;
	@include phones {
		max-width: 40%;
	}
	&--short {
		max-width: 50%;
	}
}