.eco {
	background: url('../img/dots-left-bg.png') no-repeat 0 50%, url('../img/dots-right-bg.png') no-repeat 100% 50%, url('../img/eco-bg.jpg') no-repeat 50% 50% /cover;
	@include tablets {
		background: url('../img/eco-bg.jpg') no-repeat 50% 50% /cover;
	}
}

.eco__content {
	text-align: center;
	padding-bottom: 120px;
}

.eco__title {
	text-align: center;
	color: #fff;
	font-size: 36px;
	font-weight: 600;
	@include phones {
		font-size: 32px;
	}
}

.eco__moto {
	color: #fff;
	font-size: 16px;
	margin-top: 10px;
	margin-bottom: 50px;
	text-align: center;
}

.eco__logo {
	position: relative;
}

.eco__pic {
	max-width: 100%;
	height: auto;
	@include s {
		max-width: 90%;	
	}
}

.eco__text {
	color: #fff;
	font-size: 26px;
	position: absolute;
	&--1 {
		top: 70px;
		left: 50%;
		transform: translate(-50%);
		@include tablets {
			top: 62px;
		}
		@include middle {
			top: 42px;
		}
		@include s {
			top: 25px;
		}
	}
	&--2 {
		top: 50%;
		left: 14%;
		@include desktop {
			left: 7%;
		}
		@include tablets {
			left: -5%
		}
		@include middle {
			left: -9%;
		}
		@include phones {
			left: 0;
		}
		@include s {
			left: -4%;
		}
	}
	&--3 {
		top: 50%;
		right: 16%;
		@include desktop {
			right: 9%;
		}
		@include tablets {
			right: -3%
		}
		@include middle {
			right: -5%;
		}
		@include phones {
			right: 2%;
		}
		@include s {
			right: -2%;
		}
	}
	&--4 {
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: #00717b;
		font-weight: 600;
	}
	@include middle {
		font-size: 22px;
	}
	@include phones {
		font-size: 13px;
	}
}