.lk__title {
    text-align: center;
    font-size: 45px;
    font-weight: 600;
    color: #fff;

    @include phones {
        font-size: 30px;
    }
}

.lk__moto {
    text-align: center;
    font-size: 45px;
    font-weight: 600;
    color: #74e7e6;

    @include phones {
        font-size: 30px;
    }
}

.form__main {
    display: flex;
    justify-content: center;
    margin: 70px 0;
    margin-bottom: 0;
}

.form__main-elem {
    width: 100%;
    text-align: center;
}
.form__main-row {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 10px;
    flex-wrap: no-wrap;

    @include phones {
        flex-direction: column;
    }
}

.form__main-title {
    color: #fff;
    font-size: 24px;
    flex: 0 0 50%;
}

.form__main-input {
    background-color: rgba(0, 0, 0, .3);
    border: none;
    width: 100%;
    color: #78e8e7;
    padding: 10px 20px;
    &::placeholder {
        color: #78e8e7;
        font-size: 18px;
    }
}

.text-bottom {
    text-transform: uppercase;
    color: #fff;
    font-size: 18px;
    font-weight: 600;
    margin: 20px 0;
}

.tele-icon {
    font-size: 20px;
    margin-left: 10px;
}