.lk-inner {
	background: url('../img/lk-inner-bg.jpg') no-repeat 50% 50% /cover;
	padding-bottom: 30px;
}

.balance {
	margin: 60px 0;
}

.balance__title {
	text-transform: uppercase;
	font-weight: 600;
}

.balance__amout {
	text-transform: uppercase;
	font-size: 60px;
	font-weight: 600;
	color: #00717b;
	line-height: 1;
}

.balance__currency {
	text-transform: uppercase;
	font-size: 60px;
	font-weight: 600;
	color: #ccc;
	line-height: 1;
}

.balace__desc {
	text-align: center;
}

.cards__title {
	text-align: center;
	font-weight: 600;
	margin-bottom: 10px;
}

.card {
	border: 1px solid #ccc;
	border-radius: 3px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 30px 0 10px 0;
	height: 100%;

	@include tablets {
		height: auto;
		margin-bottom: 20px;
	}
	@include phones {
		display: block;
		text-align: center;
	}
}

.card__title {
	font-weight: 600;
	text-transform: uppercase;
	display: flex;
	align-items: center;

	@include phones {
		flex-direction: column;
	}
}

.card__adress {
	font-size: 22px;
	font-weight: 600;
	color: #8d8d8d;
	margin: 35px 0;
	@include phones {
		font-size: 14px;
		word-wrap: break-word;
	}
}

.card__icon {
	margin-right: 20px;
}

.card__balance {
	margin-right: 20px;
	align-self: flex-end;
}

.card__amount {
	font-size: 24px;
	font-weight: 600;
	margin: 0 10px;
}

.card__currency {
	font-size: 24px;
}