.cost-structure {
    background: url('../img/dots-left-bg.png') no-repeat 0% 50%, url('../img/dots-right-bg.png') no-repeat 100% 50%, url('../img/token-bg.jpg') no-repeat 50% 50% /cover;
    @include tablets {
        background: url('../img/dots-left-bg.png') no-repeat -65%, url('../img/dots-right-bg.png') no-repeat 200%, url('../img/token-bg.jpg') no-repeat 50% 50% /cover;
    }
}

.cost__wrapper {
    padding: 75px 0;
}

.cost__title {
    text-align: center;
    color: #046375;
    font-weight: 500;
    font-size: 36px;
    margin-bottom: 90px;
    @include phones {
        margin-bottom: 50px;
        font-size: 32px;
    }
}

.chart__wrapper {
    position: relative;
    width: 100%;
    height: 762px;
    text-align: center;
    @include tablets {
        position: initial;
        height: auto;
    }
}

.chart__pic {
    max-width: 100%;
    height: auto;
