@keyframes blink {
    35% {
        opacity: 0;
    }
    40% {
        opacity: 1;
    }
    45% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    85% {
        opacity: 0;
    }
    90% {
        opacity: 1;
    }
    95% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}