.ico-structure {
    background: url('../img/dots-left-bg.png') no-repeat 0% 50%, url('../img/dots-right-bg.png') no-repeat 100% 50%, url('../img/ico-bg.jpg') no-repeat 50% 50% /cover;
    color: #fff;
    @include tablets {
        background: url('../img/ico-bg.jpg') no-repeat 50% 50% /cover;
    }
}

.ico__wrapper {
    padding: 75px 0;
}

.ico__title {
    text-align: center;
    color: #fff;
    font-weight: 500;
    font-size: 36px;
    margin-bottom: 90px;
    @include phones {
        font-size: 32px;
    }
}

.chart__text {
    margin-top: 70px;
}

.chart__image {
    max-width: 100%;
    height: auto;
}

