.advantages {
	background: url('../img/dots-left-bg.png') no-repeat 0% 50%, url('../img/dots-right-bg.png') no-repeat 100% 50%;
	@include tablets {
		background: none;
	}
}

.advantages__wrapper {
	padding: 0;
	padding-bottom: 75px;
}

.advantages__title {
	text-align: center;
	color: #006173;
	font-size: 36px;
	font-weight: 600;
	margin-top: 75px;
	margin-bottom: 100px;
}

.advantages__item {
	text-align: center;
}

.advantage__text {
	font-size: 24px;
	margin-top: 45px;

	@include tablets {
		font-size: 20px;
		margin-top: 20px;
	}
	@include middle {
		font-size: 16px;
	}
}

.relative {
	position: relative;
}

.advantage__wave-left {
	position: absolute;
	top: -155px;
	left: -240px;
	@include tablets {
		display: none;
	}
}

.advantage__wave-right {
	position: absolute;
	top: -155px;
	right: -240px;
	@include tablets {
		display: none;
	}
}

.advantage__pic {
	@include tablets {
		width: 200px;
		height: 200px;
	}
	@include middle {
		width: 130px;
		height: 130px;
	}
	@include phones {
		width: 80px;
		height: 80px;
	}
}