.footer {
    position: relative;
    background: url('../img/footer-bg1.jpg') no-repeat 50% 50%;
    background-size: cover;
    overflow: hidden;
}

.footer__wrapper {
    padding: 75px 0;
    padding-bottom: 40px;
}

.contacts__title {
    font-weight: 500;
    color: #fff;
    font-size: 36px;
    text-align: center;
    margin-bottom: 50px;
    @include phones {
        font-size: 32px;
    }
}

.contacts__company {
    text-align: center;
    color: #fff;
    font-weight: 500;
    font-size: 24px;
    margin: 25px 0;
}

.contacts__adress {
    font-weight: 500;
    font-size: 24px;
    color: #fff;
    text-align: center;
    margin-bottom: 55px;
}

.contacts__email {
    color: #fff;
}

.map {
    margin-bottom: 45px;
    width: 100%;
    height: 340px;
}

.form {
    margin-bottom: 25px;
}

.form__elem {
    width: 100%;
}

.form__row {
    display: flex;
    justify-content: center;
}

.email {
    width: 100%;
    border: none;
    border-radius: 3px;
    padding: 10px 20px;
    font-family: 'Open Sans';
    font-weight: 400;
    font-size: 16px;
}

.submit {
    background-color: transparent;
    border: 1px solid #fff;
    border-left: none;
    border-radius: 3px;
    padding: 10px 30px;
    cursor: pointer;
}

// .contacts__email-group {
//     margin-top: 35px;
//     margin-bottom: 20px;
// }
.footer__logo {
    margin-top: 35px;
    margin-bottom: 20px;
    @include phones {
        text-align: center;
    }
}

.footer__logo-pic {
    width: 130px;
}

.footer__text {
    color: #fff;
    line-height: 1.2;
    @include phones {
        text-align: center;
    }
}

.footer__navigation {
    display: flex;
    justify-content: flex-end;
    @include phones {
        justify-content: center;
        margin: 5px 0;
    }
}

.footer__telegram {
    // text-align: right;
    color: #fff;
}

.content {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    text-align: left;
    @include phones {
        justify-content: center;
        text-align: center;
    }
}

.email__link {
    color: #71dde0;
    &:hover {
        color: #71dde0;
    }
    &--big {
        text-transform: uppercase;
    }
}

.telegram__link {
    color: #71dde0;
    &:hover {
        color: #71dde0;
    }
}

.link-icon {
    // margin-right: 10px;
    margin-left: 10px;
}

.map__adress {
    color: #fff;
    text-align: center;
    min-height: 175px;
}

.map__country {
    font-size: 32px;
    text-transform: uppercase;
    @include tablets {
        font-size: 24px;
    }
}

.map__office {
    color: #71dde0;
    text-transform: uppercase;
    font-size: 1.2rem;
    padding: 10px 0;
}

.map__location {
    color: #71dde0;
}

.map__logo {
    margin-bottom: 30px;
    text-align: center;
}

.map__pic {
    width: 370px;
    height: 230px;
    filter: grayscale(1);
    position: relative;
    @include desktop {
        width: 310px;
        height: 190px;
    }
    @include tablets {
        width: 225px;
        height: 155px;
    }
}

.map__wrapper {
    position: absolute;
    top: 175px;
    left: 15px;
    width: 370px;
    height: 230px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 2.5rem;
    font-weight: bold;
    text-transform: uppercase;
    background-color: rgba(0, 0, 0, .3);
    @include desktop {
        width: 310px;
        height: 190px;
    }
    @include tablets {
        width: 225px;
        height: 155px;
        font-size: 24px;
    }
    @include middle {
        left: 50%;
        transform: translate(-50%);
    }
}

.social__btns {
    margin-bottom: 70px;
}
