.tokens {
    background: url('../img/dots-left-bg.png') no-repeat 0% 50%, url('../img/dots-right-bg.png') no-repeat 100% 50%, url('../img/token-bg.jpg') no-repeat 50% 50% /cover;
    position: relative;
    @include tablets {
        background: url('../img/dots-left-bg.png') no-repeat -65%, url('../img/dots-right-bg.png') no-repeat 200%, url('../img/token-bg.jpg') no-repeat 50% 50% /cover;
    }
}

.tokens__wrapper {
    padding: 75px 0;
    @include phones {
        padding-bottom: 0;
    }
}

.tokens__title {
    color: #046375;
    font-weight: 600;
    font-size: 36px;
    @include tablets {
        text-align: center;
    }
    @include phones {
        font-size: 32px;
    }
}

.description__line {
    font-weight: 600;
    font-size: 1rem;
    margin-bottom: 70px;
    @include tablets {
        margin-bottom: 30px;
    }
}

.description__price {
    color: #00717b;
    font-weight: 500;
    font-size: 18px;
    white-space: nowrap;
    margin-bottom: 25px;
}

.description__text {
    line-height: 1.2;
    margin-bottom: 42px;
    @include phones {
        font-size: 16px;
        margin-bottom: 20px;
    }
}

.description__amount-text {
    font-weight: 600;
    font-size: 17px;
}

.description__amount {
    font-weight: 600;
    font-size: 48px;
    line-height: 1;
    color: #046375;
}

.price {
    font-size: 100px;
    font-weight: 500;
    color: #dfdfdf;
    line-height: 1;
    &--green {
        color: #046375;
    }
    @include phones {
        font-size: 70px;
    }
}

.price__desc {
    text-transform: uppercase;
    color: #00717b;
    font-weight: 500;
    font-size: 21px;
    margin-bottom: 35px;
}

.token__price {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: end;
}

.price__pic {
    max-width: 100%;
    height: auto;
}

.price__logo--2 {
    @include phones {
        display: flex;
    }
}

.token__features {
    background: url('../img/tokens-features-bg.jpg') center no-repeat;
    border-radius: 3px;
    background-size: cover;
    display: flex;
    padding: 20px 50px;
    margin-top: 60px;
    margin-bottom: 40px;
    @include tablets {
        flex-direction: column;
        align-items: center;
        padding-top: 40px;
    }
}

.feature__item {
    display: flex;
    align-items: flex-start;
    @include tablets {
        width: 70%;
        justify-content: space-between;
        margin-bottom: 40px;
    }
    @include phones {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
}

.feature__logo {
    margin-right: 10px;
    @include tablets {
        margin-right: 0;
        flex-basis: 47%;
    }
}

.feature__desc {
    @include tablets {
        flex-basis: 50%;
    }
}

.feature__title {
    font-weight: 500;
    color: #fff;
    text-transform: uppercase;
    font-size: 22px;
}

.feature__text {
    color: #fff;
    line-height: 1.2;
    padding-right: 70px;
    @include tablets {
        padding-right: 0;
    }
}

.token__order {
    display: flex;
    justify-content: center;
    align-items: center;
}

.waves__icon {
    animation: blink 3s linear infinite;
}

.btn__icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 15px;
    transition: opacity .3s ease;
    &--hover {
        opacity: 0;
    }
}