.video__container {
	position: relative;
	width: 100%;
	height: 92vh;
	background-color: #000;
}


.video__player {
	width: 100%;
	height: 92vh;
	display: none;
}

.video__link {
	position: absolute;
	display: block;
	background: url('../img/video_preload.jpg') no-repeat 50% 50% /cover;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	color: #fff;
	&:hover {
		.video__icon {
		opacity: 1;
		color: #fff;
		text-decoration: none;
		}
	}
}

.video__icon {
	font-size: 4rem;
	position: absolute;
	left: 50%;
	top: 50%;
	opacity: .7;
	transform: translate(-50%, -50%);
}