.about-us {
    position: relative;
    background: url('../img/dots-left-bg.png') no-repeat 0 50%, url('../img/dots-right-bg.png') no-repeat 100% 50%;
    @include tablets {
        background: url('../img/dots-left-bg.png') no-repeat -65%, url('../img/dots-right-bg.png') no-repeat 200%;
    }
}

.about-us__wrapper {
    padding: 75px 0;
}

.about-us__title {
    text-align: center;
    font-size: 36px;
    font-weight: 500;
    color: #00717b;
    @include phones {
        font-size: 32px;
    }
}

.circles {
    display: flex;
    margin-top: 30px;
    justify-content: center;
    align-items: center;
}

.circle {
    position: relative;
}

.circle__main {
    max-width: 100%;
    height: auto;
    position: absolute;
    top: 0;
    left: 0;
    transition: opacity .3s ease;
    &:hover {
        opacity: 0;
    }
}

.circle__main--bottom {
    max-width: 100%;
    height: auto;
}

.circle__pic {
    position: relative;
    max-width: 100%;
    height: auto;
}

.circle__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: opacity .3s ease;
    &--bottom {
        opacity: 0;
        &:hover {
            opacity: 1;
        }
    }
    @include tablets {
        max-width: 90%;
    }
}

.about-us__desc {
    margin: 30px 0;
    font-family: 'Open Sans';
    font-weight: 300;
    font-size: 17px;
}

.about-us__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: baseline;
    margin-bottom: 30px;
    &--bottom {
        justify-content: center;
        flex-direction: row;
        align-items: flex-end;
        @include tablets {
            flex-direction: column;
            align-items: center;
        }
        .item__number {
            margin: 0 30px;
        }
        .item__logo {
            margin-bottom: 0;
            @include tablets {
                margin-bottom: 10px;
            }
        }
    }
}

