.item__logo {
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
}

.item__number {
    font-family: 'Open Sans';
    font-weight: 500;
    font-size: 90px;
    line-height: 76px;
}

.item__text-top {
    font-family: 'Open Sans';
    font-weight: 300;
    text-align: center;
    font-size: 34px;
    line-height: 1;
}

.item__text-bottom {
    font-family: 'Open Sans';
    font-weight: 500;
}