.faq {
	background: url('../img/dots-left-bg.png') no-repeat 0% 50%, url('../img/dots-right-bg.png') no-repeat 100% 50%;
	@include tablets {
		background: none;
	}
}

.faq__wrapper {
	padding-top: 0;
}

.faq__title {
	text-align: center;
	color: #006173;
	text-transform: uppercase;
	font-size: 36px;
	font-weight: 600;
	margin-top: 75px;
	margin-bottom: 55px;
	&--first {
		margin-top: 153px;
	}
	@include phones {
		font-size: 32px;
	}
}

.acco__trigger {
	padding: 10px;
	padding-left: 30px;
	padding-right: 20px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: #e7fbf9;
	margin-bottom: 5px;

	&--active {
		background-color: #006173;
		.acco__link {
			color: #fff;
			&:hover {
				color: #fff;
			}
		}
		.acco__triangle {
			transform: rotate(90deg);
			&:after {
				display: none;
			}
		}
	}
}

.acco__link {
	font-size: 20px;
	font-weight: bold;
	color: #006173;
	display: flex;
	width: 100%;
	justify-content: space-between;
	align-items: center;
	&:hover {
		text-decoration: none;
		color: #006173;
	}
}

.acco__triangle {
	position: relative;
    width: 0;
    height: 0;
    border: 10px solid transparent;
    border-left-color: #47e3cc;
    border-right-width: 0;
    transition: .3s ease;
    &:after {
    	content: "";
    	position: absolute;
    	top: -5px;
    	left:-8px;
    	width: 0;
	    height: 0;
	    border: 5px solid transparent;
	    border-left-color: #e7fbf9;
	    border-right-width: 0;
    }
}

.acco__text {
	padding: 20px 30px 20px 50px;
}
