.roadmap {
    overflow: hidden;
    background: url('../img/roadmap-left-bg.png') no-repeat 0% 50%, url('../img/dots-right-bg.png') no-repeat 100% 50%, url('../img/roadmap-bg.jpg') no-repeat 50% 50% /cover;
    @include middle {
        background: url('../img/rm-bg-tablets.jpg') no-repeat 50% 50% /cover;
        height: 900px;
    }
    @include phones {
        background: url('../img/rm-bg-mobile.jpg') no-repeat 50% 50% /cover;
        height: auto;
    }
}

.roadmap__wrapper {
    padding: 75px 0;
}

.roadmap__title {
    font-weight: 500;
    color: #fff;
    font-size: 36px;
    text-align: center;
    margin-bottom: 100px;
    @include phones {
        margin: 0;
        font-size: 32px;
    }
}

.roadmap__number {
    position: absolute;
    left: -30px;
    font-family: 'Proxima Nova';
    color: #002c4a;
    font-size: 100px;
    line-height: 80px;
    font-weight: 500;
    z-index: -1;
    transition: .5s ease;
    @include tablets {
        left: -35px;
    }
    @include phones {
        font-size: 60px; // left: 160px;
        position: initial;
        margin-right: 10%;
    }
}

.roadmap__item {
    position: relative;
    display: flex;
    align-items: flex-start;
    height: 100%;
    &:hover {
        .roadmap__number {
            color: #13c2fa;
        }
        .roadmap__date {
            color: #13c2fa;
        }
        .roadmap__text {
            transform: scale(1);
        }
    }
    @include tablets {
        justify-content: center;
    }
    @include middle {
        margin-bottom: 70px;
    }
    @include phones {
        justify-content: flex-start;
        margin-bottom: 0;
        height: 120px;
    }
    &--6 {
        .roadmap__number {
            @include tablets {
                left: -35px;
            }
        }
    }
    &--5 {
        .roadmap__number {
            @include tablets {
                left: -35px;
            }
        }
    }
}

.roadmap__icon {
    z-index: 10;
    margin-top: 20px; // @include phones {
    //     margin-right: 5%;
    // }
}

.roadmap__desc {
    color: #fff;
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    @include tablets {
        justify-content: center;
    }
    @include phones {
        justify-content: flex-start;
        padding-top: 12px;
        text-align: right;
        width: 100%;
    }
}

// .roadmap__pic {
//     @include phones {
//         max-width: 100%;
//         height: auto;
//     }
// }
.roadmap__date {
    text-transform: uppercase;
    font-size: 15px;
    font-weight: 600;
    transition: .5s ease;
    align-self: flex-start;
    @include phones {
        font-size: 10px;
        align-self: flex-end;
    }
}

.roadmap__text {
    transform: scale(0);
    transition: .5s ease;
    text-transform: uppercase;
    @include phones {
        font-size: 10px;
    }
    @include phones {
        transform: scale(1);
    }
}

.roadmap__lines {
    text-align: right;
}

.lines__wrap {
    display: none;
    width: 100%;
    @include tablets {
        text-align: right;
        position: absolute;
        top: 0;
        left: 0;
    }
}

.lines__pic {
    margin-left: -60%;
    height: auto;
    @include desktop {
        max-width: calc(100% + 300px);
    }
    @include tablets {
        max-width: calc(100% + 400px);
    }
}

.lines__pic-1 {
    height: auto;
}

.roadmap__desk {
    @include middle {
        display: none;
    }
}

.roadmap__mobile {
    display: none;
    @include middle {
        display: initial;
    }
}

.content__wrap {
    position: relative;
}