.close {
    position: absolute;
    top: 10px;
    right: 10px;
}

.modal-content {
    align-items: center;
    color: #999999;
    padding: 40px 0;
}

.modal__title {
    text-align: center;
    color: #11c7ef;
    font-size: 30px;
    font-weight: 600px;
}

.modal__text {
    text-align: center;
    color: #6e6e6e;
    margin: 40px 0;
}

.form__row {
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
}

.input__block {
    display: flex;
}

.block__title {
    font-size: 14px;
}

.modal__link {
    color: #11c7ef;
    text-decoration: none;
    &:hover {
        color: #11c7ef;
        text-decoration: underline;
    }
}

.form__check:checked {
    &+.check__label {
        &:before {
            background: url('../img/yes-icon.png') center no-repeat;
        }
    }
}

.check__label {
    position: relative;
    padding-left: 20px;
    &:before {
        content: '';
        position: absolute;
        top: 3px;
        left: 0;
        width: 15px;
        height: 15px;
        border: 1px solid #ccc;
    }
}

.input__image {
    position: relative;
    border: 1px solid #ccc;
    border-right: none;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fbfbfb;
    &:before {
        content: "";
        position: absolute;
        border: 5px solid transparent;
        background-color: #fbfbfb;
        border-left: 5px solid #ccc;
        top: 50%;
        right: -10px;
        transform: translateY(-50%);
    }
    &::after {
        content: "";
        position: absolute;
        border: 5px solid transparent;
        border-left: 5px solid #fbfbfb;
        top: 50%;
        right: -8px;
        transform: translateY(-50%);
    }
}

.form__password-forget {
    margin-bottom: 20px;
}

.form__input {
    line-height: 15px;
    width: 100%;
    font-size: 14px;
    padding: 5px 10px;
    border: 1px solid #ccc;
}