.blog {
	background: url('../img/dots-left-bg.png') 0% 50% no-repeat, url('../img/dots-right-bg.png') no-repeat 100% 50%, url('../img/token-bg.jpg') no-repeat 50% 50% /cover;
	min-height: 700px;
	@include tablets {
		background: url('../img/token-bg.jpg') no-repeat 50% 50% /cover;
	}
}

.blog__title {
	text-align: center;
	color: #006173;
	font-size: 36px;
	font-weight: 600;
	margin-bottom: 55px;
}

.blog__tizer {
	position: relative;
	margin-bottom: 20px;
}

.tizer__link {
	display: block;
	opacity: 1;
	transition: opacity .3s ease;
	z-index: 5;
	&:hover {
		opacity: .7;
	}
}

.tizer__pic {
	max-width: 100%;
	min-height: 307px;
	@include tablets {
		min-height: 230px;
	}
	&--small {
		min-height: 230px;
	}
}

.tizer__title {
	position: absolute;
	background-color: rgba(0, 0, 0, .5);
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	color: #fff;
}

.tizer__icon {
	font-size: 4rem;
	margin-right: 10px;
}

.tizer__name {
	font-size: 2rem;
	font-weight: bold;
}

.tizer__date {
	color: #ccc;
	font-size: 1.5rem;
	font-weight: bold;
}

.blog__desc {
	font-size: 1.5rem;
	color: #006173;
	font-weight: bold;
	&--bottom {
		@include tablets {
			margin-top: 30px;
		}
	}
}

.btn--blog {
	color: #006173;
	border: 2px solid #006173 !important;
	&:hover {
		border-color: rgb(17, 199, 239)!important;
	}
}

.separator {
	width: 100%;
	height: 3px;
	background-color: #006173;
	margin-top: 20px;
	margin-bottom: 40px;
}