.partners {
	background: url('../img/dots-left-bg.png') 0% 50% no-repeat, url('../img/dots-right-bg.png') no-repeat 100% 50%, url('../img/eco-bg.jpg') no-repeat 50% 50% /cover;
	min-height: 700px;
	@include tablets {
		background: url('../img/eco-bg.jpg') no-repeat 50% 50% /cover;
	}
}

.partners__title {
	color: #fff;
	text-align: center;
	font-size: 46px;
	font-weight: 600;
	@include phones {
		font-size: 32px;
	}
}


.partners__content {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	padding: 50px 0;
	padding-top: 150px;
}

