.partners__item {
	flex: 0 0 24%;
	min-height: 100%;
	background-color: #ffff;
	opacity: .7;
	margin: 5px;
	transition: opacity .3s ease;
	height: 90px;
	&:hover {
		opacity: 1;
	}
	@include middle {
		flex: 0 0 49%;
	}
		@include phones {
		flex: 0 0 100%;
	}
}	

.partners__link {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
}

.partners__logo {
	max-width: 80%;
	height: auto;
	@include phones {
		max-width: 40%;
	}
}
