.section {
    width: 100%;
}

.maincontent {
    position: relative;
    overflow: hidden;
}

.header__container {
    width: 90;
    max-width: 1690px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;
    @include tablets {
        padding: 0 30px;
    }
    @include phones {
        flex-wrap: wrap;
    }
}

.container-wide {
    padding: 0;
}

.container__rel {
    position: relative;
}

.navigation {
    .nav__bar {
        @include tablets {
            display: none;
        }
    }
}

.footer__navigation {
    .nav__list {
        flex-direction: column;
        @include tablets {
            flex-direction: column;
        }
        @include phones {
            text-align: center;
        }
    }
}

.nav__list {
    display: flex;
    flex-wrap: wrap;
}

.nav__link {
    display: inline-block;
    color: #fff;
    font-size: 14px;
    text-transform: uppercase;
    margin-right: 15px;
    transition: .5s ease;
    &:hover {
        color: #11c7ef;
        text-decoration: none;
    }
}

.btn {
    display: inline-block;
    color: #fff;
    padding: 20px 60px;
    font-size: 20px;
    line-height: 8.55px;
    border: 1px solid #fff;
    border-radius: 3px;
    margin: 10px 0;
    transition: .3s ease;
    &:hover,&.active {
        color: #11c7ef;
        text-decoration: none;
        border-color: #11c7ef;
    }
    &--login {
        padding: 6px 25px 6px 5px;
        font-size: 14px;
        flex: 0 0 100px;
        @include tablets {
            padding: 5px;
        }
        &:last-child{
            margin-left: 10px;
        }
    }
    &--token {
        background-size: cover;
        display: flex;
        align-items: center;
        color: #00717b;
        border-color: #00717b;
        padding: 30px 60px 30px 85px;
        font-weight: 500;
        font-size: 20px;
        position: relative;
        @include desktop {
            padding-right: 30px;
            white-space: nowrap;
        }
        &:hover {
            .btn__icon--hover {
                opacity: 1;
            }
        }
    }
    &--modal {
        color: #fff;
        background-color: #11c7ef;
        font-size: 16px;
        font-weight: 600;
        width: 100%;
        padding: 15px;
        margin: 0;
        &:hover {
            color: #fff;
        }
    }
    &--form {
        background: none;
        text-transform: uppercase;

        @include phones {
            white-space: nowrap;
        }
    }
    &--tele {
        text-transform: uppercase;
        padding: 15px 20px;
    }
    &--lk {
        color: #0f7f84;
        border-color: #0f7f84;
        text-transform: uppercase;
        font-size: 17px;
        padding: 6px 10px 6px 10px;
        line-height: 1;
        &:hover {
            color: #0f7f84;
            border-color: #0f7f84;
        }
    }
    &--profile {
        color: #ccc;
        border-color: #ccc;
        text-transform: uppercase;
        font-weight: 600;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: 0;
        width: 80%;
        height: 50px;
        margin-right: 10px;
        &:last-child{
            margin-right: 0;
        }
    }
    &--card {
        background-color: #11c7ef;
        border-color: #11c7ef;
        text-transform: uppercase;
        font-weight: 600;
        &:hover {
            color: #fff;
        }

        @include phones {
            padding: 20px 25px;
        }
    }
    &--more {
        color: #00717b;
        border-color: #00717b;
        padding: 20px 50px 20px 50px;
        font-weight: 600;
        font-size: 20px;
        margin-top: 50px;
    }

}

.btn__icon {
    margin-right: 10px;
}

.login__icon {
    margin-right: 10px;
    @include tablets {
        margin-right: 0;
    }
}

.particles-js {
    position: absolute;
    width: 20%;
    height: 90%;
    bottom: 0;
    z-index: 0;
    &--left {
        background: url('../img/dots-right.png') right no-repeat;
        background-size: cover;
        left: 0;
        transform: scale(-1, 1);
    }
    &--right {
        background: url('../img/dots-right.png') right no-repeat;
        background-size: cover;
        right: 0;
    }
    @include tablets {
        display: none;
    }
}

.wrapper {
    position: relative;
    width: 100%;
    padding: 75px 0;
    z-index: 10;
}

.sidebar {
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    right: 5%;
    z-index: 30;
    @include tablets {
        display: none;
    }
}

.sidebar__item {
    padding-bottom: 10px;
}

.sidebar__link {
    display: flex;
    justify-content: center;
    position: relative;
    color: #71dde0;
    font-family: 'Comp Pro';
    font-size: 18px;
    text-transform: uppercase;
    &:hover {
        color: #71dde0;
        text-decoration: none;
        .link__label {
            opacity: 1;
            transform: scale(1);
        }
        &:after {
            box-shadow: 0 0 0 1px #fff;
        }
    }
    &:after {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: -30px;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background-color: #2098df;
    }
    &--active {
        .link__label {
            opacity: 1;
            transform: scale(1);
        }
        &:after {
            box-shadow: 0 0 0 1px #fff;
        }
    }
}

.link__label {
    display: inline-block;
    opacity: 0;
    transform: scale(0);
    transition: .5s ease;
}

.countdown {
    display: flex;
    justify-content: center;
    align-items: baseline;
    color: #fff;
    font-size: 19px;
    margin-bottom: 60px;
    @include phones {
        flex-direction: column;
        align-items: center;
    }
}

.clock {
    margin-left: 10px;
    display: flex;
    @include phones {
        margin: 0;
    }
}

.clock__cell {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 5px;
    &:last-child {
        margin-right: 0;
    }
}

.cell__number {
    background-color: rgba(#07789f, .4);
    font-size: 30px;
    font-weight: 600;
    padding: 5px 20px;
    padding-top: 15px;
    padding-bottom: 10px;
    line-height: 1;
    @include phones {
        padding-top: 15px;
        padding-bottom: 10px;
    }
}

.cell__text {
    font-size: 14px;
}

