.hamburger {
    display: none;
    margin-right: 20px;
    @include tablets {
        display: inline-block;
    }
}

.ham__menu {
    position: relative;
    width: 30px;
    height: 4px;
    background-color: #fff;
    margin-bottom: 3px;
    &:last-child {
        margin-bottom: none;
    }
}

.hamburger__menu {
    background-color: #0f7f84;
    z-index: -30;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: none;
    flex-direction: column;
    align-items: center;
    opacity: 0;
    transition: opacity 1s ease;
    &--active {
        z-index: 30;
        opacity: 1;
    }
}

.ham__nav {
    width: 100%;
    margin-bottom: 20px;
}

.close-btn {
    display: block;
    align-self: flex-end;
    margin: 20px;
}

.ham__link {
    display: block;
    width: 100%;
    padding: 10px 0;
    text-transform: uppercase;
    color: #fff;
    text-align: center;
    font-size: 20px;
    transition: .3s ease;
    &:hover {
        // background-color: #11c7ef;
        background-color: #46c6cc;
        color: #fff;
        text-decoration: none;
    }
}